<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">

                    <div class="modal-header">
                        <slot name="header"> </slot>
                    </div>

                    <div class="modal-body">
                        <slot name="body"> </slot>
                    </div>

                    <div class="modal-footer">
                        <slot name="footer">
                            <button class="modal-default-button" @click="$emit('close')">OK</button>
                        </slot>
                    </div>

                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name: "VModal",
    components: {},
    props: {},
    data() {
        return {};
    },
    methods: {},
};
</script>

<style>
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 0.3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    font-size: 14px;
    line-height: 20px;
    width: 240px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
}

.modal-body a:link,
.modal-body a:visited,
.modal-body a:active,
.modal-body a:hover {
  color: #1b41da;
}

.modal-body a {
    text-decoration: none;
}

.modal-header h3 {
    margin-top: 0;
}

.modal-body {
    margin: 20px 0;
}

.modal-default-button {
    background: #4560f7;
    border: none;
    width: 140px;
    padding: 10px;
    border-radius: 4px;
    color: white;
    cursor: pointer;
    margin-top: 16px;
}

@media (min-width: 600px) {
    .modal-container {
        width: 460px;
    }
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
</style>
